.about-root img {
  max-width: 35vw;
  max-height: 60vh;
  object-fit: cover;
  border-radius: 50px;
  border: 2px solid #ccfcfc;
}

.about-root p {
  width: 50%;
}

.image-and-caption {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.image-and-caption p {
  width: fit-content;
  text-align: center;
}

.image-and-caption hr {
  opacity: 0.4;
  margin: 1.5vmax 0;
}

.content {
  display: flex;
  align-items: flex-start;
  padding-top: 2vmax;
}

@media screen and (max-width: 1600px) {
  article {
    min-height: 1000px;
  }
}
@media screen and (max-width: 650px) {
  .about-root img {
    max-height: none;
    max-width: 350px;
    width: 95%;
    height: 500px;
    margin: 30px;
    margin-bottom: 10px;
  }

  .about-root p {
    width: 85%;
  }

  .image-and-caption {
    margin-bottom: 15px;
  }

  .content {
    flex-direction: column-reverse;
    align-items: center;
  }
}
